.stat__container {
    width: 100%;
    background: #252632;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.8);
    border-radius: 3px;
}

.stat__header {
    background: #1F1F29;
    border-radius: 3px 3px 0px 0px;
    width: calc(100% - 32px);
    height: 50px;
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__title {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
}

.title__input {
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #595D6E;
    outline: none;
    width: 168px;
    padding: 8px 0;
    padding-left: 32px;
    border: 1px solid #39394A;
    box-sizing: border-box;
    border-radius: 3px;
    background: #1F1F29;

}
.title__input::placeholder {
    color: #595D6E;
}
.input__wrapper {
    position: relative;
}

.header__line {
    color: #595D6E;
    cursor: pointer;
    transition: .3s all;
}

.header__line:hover {
    transition: .3s all;
    color: #71778f;
}

.title__search {
    position: absolute;
    top: 9px;
    left: 8px;
}

.team__container {
    width: calc(100% - 32px);
    padding: 0 16px;
    height: 50px;
    background: #1F1F29;
    border-radius: 0px;
    border-top: 1px solid #39394A;
    display: flex;
}

.team__container > :first-child {
    margin-right: 24px;
}

.team, .time {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
    cursor: pointer;
    transition: .3s all;
    /*height: 100%;*/
}

.time {
    justify-content: center;
    margin-right: 24px;
}

.team > span {
    transition: .3s all;
}

.time > span {
    transition: .3s all;
}

.team > :first-child {
    margin-right: 8px;
}



.teams {
    width: calc(100% - 32px);
    padding: 16px;
    padding-bottom: 35px;
}

.t {
    background: #2F2F3F;
    box-shadow: 0px 4px 16px rgba(37, 38, 50, 0.8);
    border-radius: 5px;
    width: 100%;
    display: flex;
    height: 60px;
    margin-bottom: 12px;
    align-items: center;
    transition: .3s all;
    cursor: pointer;
}

.t:hover {
    transition: .3s all;
    background: #39394a;
}

.t__number {
    margin-left: 16px;
    width: 30px;
    height: 30px;
    color: #FFF;
    font-weight: bold;
    font-size: 14px;
    border-radius: 50%;
    background: #48485C;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.3), 0px 1px 4px rgba(34, 34, 45, 0.5);
}

.t__team {
    margin-left: 30px;
}

.t__name {
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
    margin-left: 30px;
}

.t__countBet {
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #595D6E;
    margin-left: 50px;
}

.team__info {
    flex: 3;
    margin-right: 16px;
}

.info__header {
    width: calc(100% - 21px);
    padding-left: 21px;
    color: #FFFFFF;
    height: 50px;
    background: #22222D;
    border-radius: 4px 4px 0px 0px;
    margin-right: 16px;
    font-size: 14px;

    display: flex;
    align-items: center;
    font-weight: bold;
}

.info__container {
    width: 100%;
    background: linear-gradient(180deg, rgba(78, 58, 142, 0.2) 0%, rgba(47, 47, 63, 0.2) 49.31%), #2F2F3F;
    border-radius: 0px 0px 4px 4px;
}

.team__logo {
    width: 100%;
    height: 140px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.info__name {
    width: 100%;
    padding-bottom: 33px;
    font-size: 14px;
    line-height: 16px;
    font-weight: bold;
    text-align: center;
    color: #FFFFFF;
    border-bottom: 1px solid #39394A;
}

.info__min_c {
    width: calc(100% - 64px);
    margin-top: 30px;
    padding: 0 32px;
    padding-bottom: 30px;
}

.min__line {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.line__left {
    font-size: 14px;
    color: #8C90A4;
    font-weight: bold;
}

.line__right {
    font-size: 14px;
    color: #FFFFFF;
}

.team__res {
    flex: 7;
}

.header__res {
    background: #22222D;
    border-radius: 3px 3px 0px 0px;
    height: 50px;
    width: calc(100% - 32px);
    font-style: normal;
    font-size: 14px;
    color: #8C90A4;
    padding: 0px 16px;
    display: flex;
    align-items: center;
    /*justify-content: space-between;*/
}

.header__res span {
    flex: 1;
    /*font-weight: bold;*/
}

.res__container_lose, .res__container_win {
    border-radius: 3px;
    width: calc(100% - 32px);
    padding: 0 16px;
    height: 50px;
    background: #2f2f3f;
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    color: #FFF;
    font-size: 14px;
    font-weight: 500;
    position: relative;
    overflow: hidden;
}

.res__container_win::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 3px;
    background: #399241;
    border-radius: 3px;
}

.res__container_win::after {
    content: "";
    position: absolute;
    top: 0;
    z-index: 10;
    left: -16px;
    width: 32px;
    height: 100%;
    background: #399241;
    filter: blur(12px);
    border-radius: 3px;
}

.res__container_lose::before {
    content: "";
    top: 0;
    left: 0;
    width: 3px;
    height: 100%;
    background: #A93B3D;
    border-radius: 3px;
    position: absolute;
}

.res__container_lose::after {
    content: "";
    position: absolute;
    top: 0;
    z-index: 10;
    left: -16px;
    width: 32px;
    height: 100%;
    opacity: 0.6;
    background: #E85154;
    filter: blur(12px);
    border-radius: 3px;
}

.res__container_lose > span, .res__container_win > span {
    flex: 1;
}