.stat__container {
    width: 100%;
    background: #252632;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.8);
    border-radius: 3px;
}

.stat__header {
    background: #1F1F29;
    border-radius: 3px 3px 0px 0px;
    width: calc(100% - 32px);
    height: 50px;
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__title {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
}

.container {
    width: calc(100% - 48px);
    padding: 24px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    text-align: justify;
    color: #8C90A4;
}