.stat__container {
    width: 100%;
    background: #252632;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.8);
    border-radius: 3px;
}

.stat__header {
    background: #1F1F29;
    border-radius: 3px 3px 0px 0px;
    width: calc(100% - 32px);
    height: 50px;
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__title {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
}

.title__input {
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #595D6E;
    outline: none;
    width: 168px;
    padding: 8px 0;
    padding-left: 32px;
    border: 1px solid #39394A;
    box-sizing: border-box;
    border-radius: 3px;
    background: #1F1F29;

}
.title__input::placeholder {
    color: #595D6E;
}
.input__wrapper {
    position: relative;
}
.title__search {
    position: absolute;
    top: 9px;
    left: 8px;
}

.container {
    background: #252632;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.8);
    width: calc(100% - 32px);
    padding: 8px 16px;
    /* padding-bottom: 8px; */
    border-radius: 4px;
}

.section__title {
    margin-top: 12px;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
    margin-bottom: 24px;
}

.question__block {
    background: #2F2F3F;
    border-radius: 4px;
    width: calc(100% - 32px);
    padding: 0px 16px;
    height: 40px;
    font-weight: 500;
    font-size: 14px;
    color: #8C90A4;
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    cursor: pointer;
    transition: .3s color;
    position: relative;
}

.question__block:hover {
    background: #39394a;
    transition: .3s color;
}

.q_img {
    position: absolute;
    right: 16px;
    top: 12px;
    transition: .3s all;
}

.answer__block {
    background: #39394A;
    border-radius: 0px 0px 4px 4px;
    width: calc(100%);
    /* padding: 13px; */
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #FFFFFF;
    height: 0;
    transition: .3s height;
}
