.wrapper {
    width: 100%;
    background: #252632;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.8);
    border-radius: 4px;
}

.header {
    background: #1F1F29;
    border-radius: 4px 4px 0px 0px;
    width: calc(100% - 18px);
    height: 50px;
    padding-left: 18px;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
    display: flex;
    align-items: center;
}

.container {
    padding: 16px;
    width: calc(100% - 32px);
    display: flex;
}

.data__container {
    background: #2F2F3F;
    box-shadow: 0px 4px 16px rgba(37, 38, 50, 0.8);
    border-radius: 4px;
    margin-right: 16px;
    flex: 3;
    padding: 25px 35px;
    font-weight: 500;
    font-size: 14px;
    line-height: 28px;
}

.c_container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.data__left {
    color: #595d6e;
    font-weight: 500;
}

.data__right {
    color: #FFF;
    font-weight: 500;
}

.line {
    width: 100%;
    height: 1px;
    background: #39394A;
    margin: 20px 0px;
}

.button__container {
    width: 280px;
}

.button {
    margin-bottom: 12px;
    background: #4C75A3;
    border-radius: 4px;
    width: 100%;
    height: 50px;
    font-size: 16px;
    line-height: 21px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #FFFFFF;
    font-weight: 500;
    text-transform: uppercase;
    cursor: pointer;
}