.wrapper {
    margin-left: 70px;
    width: calc(100% - 70px);
    height: calc(100vh - 205px);
    background: #2f2f3f;
    display: flex;
    margin-bottom: 22px;
    padding-bottom: 150px;

}

.profile__container {
    margin-left: 21px; 
    margin-top: 25px;
    width: calc(100% - 42px - 235px);
    /* height: 778px;     */
    background: #252632;
    border-radius: 4px;

}

.header__container {
    width: calc(100% - 24px);
    height: 50px;
    background: #22222D;
    display: flex;
    align-items: center;
    padding-left: 24px;
    justify-content: flex-start;
    border-radius: 4px 4px 0px 0px;
}

.header__item, .header__item__active {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #595D6E;
    text-transform: uppercase;
    margin-right: 42px;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
    transition: .3s all;
}


.header__item__active {
    color: #FFF;
    position: relative;
}

.header__item:hover {
    color: #FFF;
    transition: .3s all;
    position: relative;
}



.header__item:hover::before {
    opacity: 1;
    transition: .3s all;
}



.header__item__active::before {
    content: "";
    /* transition: inherit; */
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: #FCB90B;
    box-shadow: 0px 2px 5px rgba(252, 185, 11, 0.3);
    height: 2px;
}

.header__item::before {
    content: "";
    transition: inherit;
    position: absolute;
    top: 100%;
    left: 0;
    opacity: 0;
    width: 100%;
    background: #b88709;
    box-shadow: 0px 2px 5px rgba(252, 185, 11, 0.3);
    height: 2px;
    /* background: red; */
}


