.bet__container {
    background: linear-gradient(0deg, #2F2F3F, #2F2F3F), #2F2F3F;
    width: 100%;
    height: 140px;
    border-radius: 5px;
    overflow: hidden;
    margin-bottom: 20px;
}

.container__bets::-webkit-scrollbar-track {
    background: #282936;
}

.bet__top__win, .bet__top__lose {
    width: 100%;
    height: 59px;
    background: linear-gradient(0deg, rgba(89, 229, 116, 0.1), rgba(89, 229, 116, 0.1)), #2F2F3F;
    border-radius: 3px 3px 0px 0px;
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.bet__top__lose {
    background: linear-gradient(0deg, rgba(232, 81, 84, 0.1), rgba(232, 81, 84, 0.1)), #2F2F3F;
}

.bet__top__lose::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 3px;
    background: #A93B3D;
    border-radius: 3px;
}

.bet__top__win::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 3px;
    background: #399241;
    border-radius: 3px;
}

.bet__top__lose::after {
    content: "";
    position: absolute;
    top: 0;
    z-index: 10;
    left: -16px;
    width: 32px;
    height: 100%;
    opacity: 0.6;
    background: #E85154;
    filter: blur(12px);
    border-radius: 3px;
}

.bet__top__win::after {
    content: "";
    position: absolute;
    top: 0;
    z-index: 10;
    left: -16px;
    width: 32px;
    height: 100%;
    background: #399241;
    filter: blur(12px);
    border-radius: 3px;
}

