.inventory__wrapper {
    width: 455px;
    background: #252632;
    box-shadow: 0px 4px 16px rgba(37, 38, 50, 0.8);
    border-radius: 4px;
    margin-top: 22px;
    height: auto;
    padding-bottom: 24px;
    /*position: sticky;*/
    /*position: -webkit-sticky;*/
    top: 0;
}
.switcher {
    width: 100%;
    height: 70px;
    background: #1F1F29;
    border-radius: 3px 3px 0px 0px;
    display: flex;
    position: relative;
}
.switcher__text {
    margin-left: 55px;
}
.menu__line {
    height: 38px;
    width: 2px;
    top: 15px;
    left: 50%;
    margin: 0;
    position: absolute;
    background: rgba(140, 144, 164, 0.2);
}
.switcher__item {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #8C90A4;
    border-bottom: 2px solid #1F1F29;
    cursor: pointer;
    transition: .4s all;
    position: relative;
}


.switcher__icon {
    /*margin-left: 24px;*/
    /*margin-right: 10px;*/
    position: absolute;
    top: 22px;
    left: 22px;
    transition: .4s opacity;
}

.balance__container {
    background: #2F2F3F;
    box-shadow: 0px 4px 16px rgba(37, 38, 50, 0.8);
    border-radius: 5px;
    width: calc(100% - 48px);
    height: 80px;
    display: flex;
    position: relative;
    margin: 24px;
}

.balance__block {
    display: flex;
    height: 100%;
    align-items: flex-start;
    width: 100px;
    margin-left: 20px;
    justify-content: center;
    flex-direction: column;
}

.balance__title {
    margin-bottom: 6px;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #8C90A4;
}

.balance__value {
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 21px;
    color: #FFFFFF
}

.balance__line {
    position: absolute;
    top: 18px;
    left: 116px;
    width: 2px;
    height: 38px;
    background: rgba(140, 144, 164, 0.2);
}

.container {
    transition: .3s opacity;
    /*padding: 24px;*/
}
.checkbox_false,.checkbox_true {
    border: 1px solid #595D6E;
    box-sizing: border-box;
    border-radius: 4px;
    width: 24px;
    height: 24px;
    cursor: pointer;
}
.checkbox_true {
    background: #FCB90B;
    border-radius: 4px;
    background-image: url("img/accept.png");
    background-repeat: no-repeat;
    background-position: center;
}
.input__container {
    position: absolute;
    top: 0;
    right: 16px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #8C90A4;
}

.input__container > :first-child {
    margin-right: 14px;
}

.item__container {
    z-index: 10;
    margin-left: 24px;
    display: flex;
    margin-top: 24px;
    flex-wrap: wrap;
    min-height: 180px;
    max-height: 501px;
    overflow: scroll;
    padding-bottom: 10px;
    transform: translateX(-2px);
    padding-left: 2px;
    /*justify-content: space-between;*/
}

.item__container > :nth-child(3n) {
    margin-right : 0;
}
::-webkit-scrollbar {
    /*height: 100%;*/
    width: 4px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 2px;
}

::-webkit-scrollbar-button {
    display: none;
}

/* Track */
::-webkit-scrollbar-track {
    background: #252632;
}
::-webkit-scrollbar-corner {
    display: none;
}
/* Handle */
::-webkit-scrollbar-thumb {
    /*background: #888;*/
    width: 4px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 2px;
    height: 130px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}




