    cursor: pointer;
    margin-top: 10px;
    /*background: radial-gradient(50% 50% at 0% 0%, rgba(204, 44, 223, 0.05) 0%, rgba(46, 70, 192, 0) 100%, rgba(204, 44, 223, 0) 100%), radial-gradient(100% 100% at 100% 100%, rgba(204, 44, 223, 0.1) 0%, rgba(204, 44, 223, 0) 100%), #2F2F3F;*/
    /*background: radial-gradient(50% 50% at 0% 0%, rgba(94, 152, 217, 0.05) 0%, rgba(94, 152, 217, 0) 100%), radial-gradient(100% 100% at 100% 100%, rgba(94, 152, 217, 0.1) 0%, rgba(94, 152, 217, 0) 100%), #2F2F3F;;*/
    /*background: #2F2F3F;*/
    border-radius: 4px;
    width: 125px;
    height: 154px;
    position: relative;
