.item:hover {
    border: 2px solid rgba(255, 255, 255, 0.2);
    /*transform: translate(-2px, -2px);*/
    /*margin-right: 6px;*/
    /*margin-bottom: -4px;*/
    transition: .2s border;
    /*margin-left: -4px;*/
}

.item__photo {
    margin-top: 10px;
}
.item__price {
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 120%;
    color: #FCB90B;
    position: absolute;
    left: 16px;
    top: 12px;
}
.item__text {
    position: absolute;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    top: 111px;
    left: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    color: #FFFFFF;
}
.button {
    height: 44px;
    width: calc(100% - 52px);
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #252632;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FCB90B;
    box-shadow: 0px 4px 16px rgba(37, 38, 50, 0.8);
    border-radius: 4px;
    margin-left: 24px;
    text-transform: uppercase;
    border: 2px solid #FCB90B;
    cursor: pointer;
    /* transition: .2s all; */
    transition: .2s background, .2s color, .2s border;
}
.button:hover {
    /* background: #1F1F29;
    color: #FCB90B;
    border: 2px solid #FCB90B;
    transition: .2s all; */
}
.input__wrapper {
    width: calc(100% - 48px);
    display: flex;
    margin-left: 24px;
    margin-bottom: -10px;
    /*height: 44px;*/
}
.input_1, .input_2 {
    padding-left: 14px;
    padding-top: 11px;
    padding-bottom: 12px;
    background: #252632;
    border: 1px solid #595D6E;
    box-sizing: border-box;
    border-radius: 4px;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 21px;
    color: #8C90A4;
    outline: none;
}
.input_1 {
    width: 268px;
    margin-right: 10px;
}
.input_2 {
    width: 129px;
}

.steam__container {
    width: 100%;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.reg__no {
    width: 150px;
    height: 150px;
    transform: translateX(15px);
    margin-top: 20px;
}

.reg__title {
    margin-top: 40px;
    color: #595D6E;
    width: 170px;
    font-size: 18px;
    font-weight: 500;
    display: flex;
    text-align: center;
    justify-content: center;
}

.reg__button {
    margin-top: 70px;
    background: #399241;
    width: 253px;
    height: 56px;
    color: #FFF;
    box-shadow: 0px 4px 16px rgba(37, 38, 50, 0.8);
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    position: relative;
    cursor: pointer;
}

.reg__button > :first-child {
    transform: translateX(15px);
}

.reg__steam {
    position: absolute;
    left: 0;
    top: 10;
}

.no__item__container {
    width: 100%;
    height: 310px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.no__item__title {
    margin-top: -10px;
    width: 180px;
    font-size: 16px;
    text-align: center;
    display: flex;
    color: #595D6E;
    font-weight: 500;
    font-size: 18px;
    align-items: center;
    transform: translateX(-8px)
}

.pot__bet {
    width: calc(100% - 48px);
    margin: 0 24px;
    height: 80px;
    background: #2F2F3F;
    margin-bottom: 24px;
    border-radius: 5px;
    color: #8C90A4;
    display: flex;
    font-size: 14px;
    justify-content: center;
    flex-direction: column;
}

.item__top {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #C0C0C2;
    position: absolute;
    top: 15px;
    left: 15px;
}


.pot__bet > :first-child {
    margin-bottom: 7px;
}

.pot__line {
    width: calc(100% - 40px);
    padding: 0px 20px;
    display: flex;
    justify-content: space-between;
}

@media (max-width: 1400px) {
    .item__container {
        margin-top: 10px;
        max-height: 355px;
        margin-bottom: 0;
    }
    .balance__container {
        margin-top: 10px;
        margin-bottom: 10px;
        height: 65px;
    }
    .switcher {
        height: 50px;
    }
    .switcher__icon {
        top: 11px;
    }
    .inventory__wrapper {
        padding-bottom: 12px;
    }
    .menu__line {
        top: 6px;
    }
}