.stat__container {
    width: 100%;
    background: #252632;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.8);
    border-radius: 3px;
}

.stat__header {
    background: #1F1F29;
    border-radius: 3px 3px 0px 0px;
    width: calc(100% - 32px);
    height: 50px;
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__title {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
}
.main__container {
    width: 100%;
    padding-top: 17px;
    /* height: 557px; */
    display: flex;
    justify-content: center;
    background: #252632;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.8);
    border-radius: 4px;
    padding-bottom: 36px;
}

.data__container {
    padding: 24px;
    padding-top: 29px;
    width: 500px;
    background: #2F2F3F;
    box-shadow: 0px 4px 16px rgba(37, 38, 50, 0.8);
    border-radius: 4px;
}

.data__title {
    font-weight: 500;
    font-size: 12px;
    width: 100%;
    text-align: center;
    color: #8C90A4;
    margin-bottom: 33px;
}

.data__desc {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #8C90A4;
    width: 100%;
    margin-bottom: 27px;
}

.input {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    /* color: #595D6E; */
    color: #FFF;
    outline: none;    
    background: #252632;
    border: 1px solid #595D6E;
    box-sizing: border-box;
    border-radius: 4px;
}

.input::placeholder {
    color: #595D6E;
}

.textarea__container {
    margin-top: 12px;
    height: 150px;
    padding: 16px;
    padding-bottom: 50px;
    width: calc(100% - 32px);
    /* background: red; */
    background: #252632;
    border: 1px solid #595D6E;
    /* box-sizing: border-box; */
    border-radius: 4px;
    position: relative;
}

.textarea {
    outline: none;
    color: #FFF;
    width: 100%;
    height: 150px;
    background: #252632;
    border: none;
    box-sizing: border-box;
    border-radius: 4px;
    resize: none;
    font-size: 14px;

}

.textarea__title {
    font-size: 12px;
    line-height: 21px;
    color: #595D6E;
    position: absolute;
    left: 16px;
    bottom: 16px;
    z-index: 10;
}

.textarea::placeholder {
    color: #595D6E;
}

.bottom__container {
    width: 100%;
    margin-top: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.checkbox__container {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #8C90A4;
    display: flex;
    align-items: center;
}

.checkbox_false,.checkbox_true {
    border: 1px solid #595D6E;
    box-sizing: border-box;
    border-radius: 4px;
    width: 24px;
    height: 24px;
    cursor: pointer;
    margin-right: 15px;
}
.checkbox_true {
    background: #FCB90B;
    border-radius: 4px;
    background-image: url("img/accept.png");
    background-repeat: no-repeat;
    background-position: center;
}

.button {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    width: 233px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #2D2F38;
    background: #FFBA00;
    border-radius: 3px;
    cursor: pointer;
}

label {
    cursor: pointer;
 }
 
 .upload_photo {
    opacity: 0;
    position: absolute;
    z-index: -1;
 }
